<template>
    <el-container style="height: 500px; border: 1px solid #eee">
        <el-header class="el-icon-s-home" style=" font-size: 40px; background-color:#409EFF; color:azure">
            教考系统</el-header>
        <el-footer>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item index="1"><router-link to="/teacher" tag="span">账号信息</router-link></el-menu-item>
                <el-menu-item index="2"><router-link to="/questionBank" tag="span">题库管理</router-link></el-menu-item>
                <el-menu-item index="3"><router-link to="/examPaper" tag="span">试卷管理</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examSchedule" tag="span">考试安排</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/examGrading" tag="span">阅卷</router-link></el-menu-item>
                <el-menu-item index="4"><router-link to="/class" tag="span">班级管理</router-link></el-menu-item>
                <el-menu-item index="5"><router-link to="/studentLogin" tag="span">退出登录</router-link></el-menu-item>
            </el-menu>
        </el-footer>


        <el-main>

            <el-form :model="inputData" :inline="true" label-width="50px" style="width: 500px;"
                class="demo-form-inline">
                <el-form-item prop="key">
                    <el-input v-model="inputData" placeholder="请输入关键字以搜索"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="likeGet">查询</el-button>
                </el-form-item>
            </el-form>


            <el-table :data="classData" style="width: 100%">
                <el-table-column prop="classNum" label="班级编号" width="200">
                </el-table-column>
                <el-table-column prop="className" label="班级名称" width="200">
                </el-table-column>
                <el-table-column prop="courseNum" label="所属课程" width="200">
                </el-table-column>
                <el-table-column prop="classTime" label="上课时间" width="200">
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="text" @click="selectByClassID(scope.row.classNum)">查看</el-button>

                        <el-dialog title="学生信息" :visible.sync="dialogTableVisible">
                            <el-table :data="formData.students">
                                <el-table-column property="studentId" label="学号" width="150"></el-table-column>
                                <el-table-column property="studentName" label="学生姓名" width="200"></el-table-column>
                                <el-table-column property="studentSex" label="性别" width="150"></el-table-column>
                                <el-table-column label="操作" width="300">
                                    <template slot-scope="scope">
                                        <el-button @click="deleteStudent(scope.row.studentID)" type="text"
                                            size="medium">删除</el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-dialog>
                    </template>
                </el-table-column>
            </el-table>

        </el-main>
    </el-container>

</template>

<script>
import axios from 'axios';
export default {
    data() {
        return {
            classData: [],
            formData: [],
            //班级学生信息
            form: [{
                studentID: '9527',
                studentName: '刘龙',

            }, {
                studentID: '9528',
                studentName: '朴昌范',
            }],
            inputData: '',
            formLabelWidth: '120px',
            dialogTableVisible: false,
        }
    },
    methods: {
        handleClick(row) {
            console.log(row);
        },
        onSubmit() {
            console.log('submit!');
        },
        //模糊查询
        likeGet() {
            axios.get(`/api/class/inputData/${this.inputData}`).then((result) => {
                this.classData = result.data.data;
            });

        },
        //查询全部
        getAll() {
            axios.get("/api/class").then((result) => {
                this.classData = result.data.data;
            });
        },
        //根据班级编号查询学生信息
        selectByClassID(classID){
            axios.get(`/api/class/classId/${classID}`).then((result) => {
                this.formData=result.data.data;
                this.dialogTableVisible = true;
            });

        },
        //删除学生
        deleteStudent(studentID){
            axios.delete(`/api/class/${studentID}`).then((result) => {
                if(result.data.code==1){
                    this.open2();
                }
                else{
                    this.open3();
                }
                
            });

        }
    },
    mounted() {
        this.getAll();

    },
}
</script>

<style scoped>
.el-container {
    min-height: 100vh; /* 至少占据整个视口高度 */
    max-height: 1200px; /* 最大高度限制 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    display: flex;
    flex-direction: column;
  }
  
  .el-header, .el-footer {
    flex: 0 0 auto; /* 固定高度 */
    background-color: #409EFF;
    color: azure;
    padding: 10px;
  }
  
  .el-main {
    flex: 1 1 auto; /* 自动占据剩余空间 */
    overflow-y: auto; /* 如果内容超出高度，显示滚动条 */
    padding: 20px;
    box-sizing: border-box;
  }
</style>